<template>
  <div class="container">
    <div class="row">
      <div class="empty col-md-12 d-flex justify-content-center mt-5 px-0" v-if="categories.length === 0">
        <p class="text-secondary">No commands found</p>
      </div>
      <div class="col-md-12 px-0">
        <div v-for="category in categories" v-bind:key="category.id">
          <!-- Categories -->
          <div class="category card mt-5">
            <div class="card-body position-relative">
              <div class="d-flex justify-content-between">
                <h5 class="card-title">{{ category.name }}</h5>
              </div>
              <p class="card-text">{{ category.description }}</p>
              <div class="empty-category" v-if="category.commands === null">
                No commands found
              </div>
              <div @click="$bvModal.show(command.name + '-' + command.id.toString())" class="command d-flex justify-content-between" v-for="command in category.commands" v-bind:key="command.id" v-else>
                <code>{{ command.name }}</code>
                <p>{{ command.description }}</p>
                
                
                
                <b-modal v-bind:id="command.name + '-' + command.id.toString()" hide-footer>
                  <b-form @submit.prevent="onCommandUpdate">
                    <b-form-group label="Command" label-for="command">
                      <b-form-input name="command" type="text" v-bind:value="command.name" placeholder="Enter command" required></b-form-input>
                    </b-form-group>
                    <b-form-group label="Description" label-for="description">
                      <b-form-input name="description" type="text" v-bind:value="command.description" placeholder="Enter description"></b-form-input>
                    </b-form-group>
                    <div class="actions d-flex">
                      <b-button type="submit" variant="primary">Update</b-button>
                      <b-button type="delete" variant="danger" class="ml-2">Delete</b-button>
                    </div>
                  </b-form>
                </b-modal>
              </div>

              <!-- NEW COMMAND -->
              <p class="new-command-btn mx-auto my-2" @click="$bvModal.show(category.name + '-' + category.id.toString())">New command</p>
              <b-modal v-bind:id="category.name + '-' + category.id.toString() " title="New command" hide-footer>
                <b-form @submit.prevent="onCommandSave(category.id)">
                  <b-form-group label="Command" label-for="command">
                    <b-form-input name="command" v-model="commandForm.Name" type="text" placeholder="Enter command" required></b-form-input>
                  </b-form-group>
                  <b-form-group label="Description" label-for="description">
                    <b-form-input name="description" v-model="commandForm.Description" type="text" placeholder="Enter description"></b-form-input>
                  </b-form-group>
                  <div class="actions d-flex">
                    <b-button type="submit" variant="primary">Save</b-button>
                  </div>
                </b-form>
              </b-modal>
              <!-- NEW COMMAND END -->
            </div>
          </div>
          
          <!-- Categories end -->
        </div>
        
        
        <!-- NEW CATEGORY MODAL-->
        <div class="new-category mt-5">
          <div class="new-category mx-auto">
            <b-button v-b-modal.new-category variant="dark">Add category</b-button>
          </div>
          <b-modal id="new-category" title="Add category">
            <div>
              <b-form @submit.prevent="onCategorySave">
                <b-form-group label="Name" label-for="platform-name">
                  <b-form-input id="platform-name" v-model="form.Name" type="text" placeholder="Enter name" required></b-form-input>
                </b-form-group>

                <b-form-group label="Description" label-for="platform-description">
                  <b-form-input id="platform-description" v-model="form.Description" placeholder="Enter description"></b-form-input>
                </b-form-group>

                <b-button type="submit" variant="primary">Save</b-button>
              </b-form>
            </div>
          </b-modal>
        </div>
        <!-- NEW CATEGORY MODAL END-->
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import axios from 'axios'

@Component
class Commands extends Vue {

  categories = [];
  
  form = {
    Name: '',
    Description: '',
    PlatformId: null
  }
  
  commandForm = {
    Name: '',
    Description: '',
    CategoryId: null
  }
  
  mounted(){
    this.$root.$on('update-commands', (platform) =>{
      let url = "https://localhost:5001/api/categories/platform/" + platform.id;
      this.form.PlatformId = platform.id;
      axios.get(url)
          .then(response => {
            this.categories = response.data;
            console.log(response);
          })
          .catch(error => {
            console.log(error);
          });
    });
    
   axios.get("https://localhost:5001/api/categories")
   .then(response => {
      console.log(response);
      this.categories = response.data;
   })
   .catch(error => {
     console.log(error);
   });
  }

  onCategorySave(){
    let url = "https://localhost:5001/api/categories";
    axios.post(url, this.form)
        .then(response => {
          console.log('NEW CATEGORY',response);
          this.categories.push(response.data);
          this.$bvModal.hide('new-category');
          this.form.Name = '';
          this.form.Description = '';
        })
        .catch(error => {
          console.log(error);
        });
  }

  onCommandSave(categoryId) {
    this.commandForm.CategoryId = categoryId;
    let url = "https://localhost:5001/api/commands";
    axios.post(url, this.commandForm)
        .then(response => {
          console.log('NEW command',response);
          this.commands.push(response.data);
          this.$bvModal.hide('new-category');
          this.commandForm.Name = '';
          this.commandForm.Description = '';
          this.commandForm.CategoryId = null;
        })
        .catch(error => {
          console.log(error);
        });
  }
  
  onCommandUpdate() {
    console.log('COMMAND UPDATE');
  }
  
}
export default Commands;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .new-command-btn {
    text-align: center;
  }
  
  .new-command-btn:hover{
    cursor: pointer;
    color: #0056b3;
  }
  
  .command:hover{
    cursor: pointer;
  }
  
  .category {
    animation: fadeIn 0.3s;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
</style>
